#about{
    height: 100vh;
    width: 80%;
    margin: auto;
    padding: 5rem;
}
.about_heading{
    text-align: center;
}
hr {
    overflow: visible;
    padding: 0;
    border: none;
    width: 15%;
    margin: auto;
    border-top: medium double #ffc234;
    color: #ffc234;
    text-align: center;
}
.about_container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 5rem auto;
}
.about_img{
    width: 300px;
    border-radius: 52% 48% 69% 31% / 69% 50% 50% 31%;
}
.about_details{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}
.about_details h2,p{
    width: 80%;
    margin: 0.5rem auto;
}
.email{
    text-decoration: none;
    color: orange;
}
/* ******************Responsive*********************** */
/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (max-width: 821px) {
   .about_container{
    flex-direction: column;
   }
   .about_details h2,p{
    width: 100%;
   }
   #about{
    padding: 3rem;
   }
   .about_img{
    width: 200px;
    margin-bottom: 1rem;
   }
}
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  #about{
    width: 100%;
  }
}

